import React from 'react';
import styled from '@emotion/styled';
import {graphql} from 'gatsby';
import slugify from '../../utils/slug';
import Layout from '../layout';
import Comments from '../comments';
import SEO from '../seo';
import BlogHeading from './heading';

const BlogContent = styled.article`
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: ${props => props.theme.colors.primary};
  }
`;

export default ({data: {markdownRemark: post}, pageContext}) => {
  return (
    <Layout header={<BlogHeading {...post.frontmatter} />}>
      <SEO
        title={post.frontmatter.title}
        description={post.excerpt}
        keywords={post.frontmatter.tags}
      />
      <BlogContent dangerouslySetInnerHTML={{__html: post.html}} />
      <Comments id={slugify(pageContext.slug)} title={post.frontmatter.title} />
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: {slug: {eq: $slug}}) {
      html
      excerpt
      frontmatter {
        ...Heading
        title
        tags
      }
    }
  }
`;
