import {css} from '@emotion/core';
import {darken} from 'polished';
import {MIN_TABLET_MEDIA_QUERY} from 'typography-breakpoint-constants';

// https://www.color-hex.com/color-palette/73238
const colorPalette = ['#0e9aa7', '#96ceb4', '#ffcc5c', '#ff6f69', '#ced07d'];
const primaryColor = colorPalette[3];
const accentColor = colorPalette[2];
const backgroundColor = 'white';

export default {
  colors: {
    primary: primaryColor,
    accent: accentColor,
    palette: colorPalette,
    background: backgroundColor,
  },
  gradient: css`
    background-color: ${primaryColor};
    background-image: linear-gradient(
      to right,
      ${colorPalette.slice(2, 4).join(', ')}
    );
    ${MIN_TABLET_MEDIA_QUERY} {
      background-image: linear-gradient(to right, ${colorPalette.join(', ')});
    }
  `,
  shadow: css`
    box-shadow: 0px 2px 2px 0px ${darken(0.2, backgroundColor)};
  `,
};
