import idx from 'idx';
import React from 'react';
import {graphql} from 'gatsby';
import {css} from '@emotion/core';
import styled from '@emotion/styled';
import {rhythm} from '@kevinastone/theme-blog';

const backgroundImage = img => css`
  background-image: url(${img.src}), url(${img.base64});
`;

const Container = styled.div`
  background-position: center;
  background-size: cover;
  color: white;
  margin-bottom: ${rhythm(1.5)};
  padding: ${rhythm(2)} 0;
  text-align: center;
  ${props => props.theme.shadow}
  ${props => (props.img ? backgroundImage(props.img) : props.theme.gradient)}
`;

export default ({hero, children}) => {
  const img = idx(hero, _ => _.childImageSharp.fluid);
  return <Container img={img}>{children}</Container>;
};

export const query = graphql`
  fragment Hero on frontmatter_2 {
    hero {
      childImageSharp {
        fluid(
          quality: 80
          maxWidth: 1920
          duotone: {highlight: "#ffcc5c", shadow: "#ff6f69", opacity: 50}
        ) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
