import React from 'react';
import {Provider} from 'react-redux';
import debounce from '@kevinastone/debounce-promise';

import SearchForm from './components/form';
import store from './store';
import api from './api';

const search = debounce(api, 100);

export default () => (
  <Provider store={store}>
    <SearchForm api={search} />
  </Provider>
);
